#head {
    padding: 20px;
    background: #ffffff;
}

.yujian_content_name {
    font-size: 14px;
    color: #333333;
}

.yujian_text {
    font-size: 22px;
    position: relative;
    display: inline;
}

p.yujian_text:after {
    content: "";
    width: 5px;
    height: 5px;
    background: red;
    position: absolute;
    border-radius: 3px;
    right: 2px;
    top: 2px;
}

.scroll_box {
    overflow-x: auto;
    white-space: nowrap;
    padding: 0 20px;
    margin-top: 10px;
}

.scroll_item {
    padding: 0 20px;
    width: 150px;
    height: 90px;
    background: #ffffff;
    border-radius: 10px;
    display: inline-block;
    margin-right: 10px;
    text-align: center;
}

.scroll_item_img {
    width: 20px;
}

.scroll_item_inner_div {
    padding-top: 20px;
}

.scroll_item_text {
    position: relative;
    font-size: 13px;
}

.scroll_item_text:after {
    content: "";
    width: 20px;
    height: 2px;
    background: #333333;
    position: absolute;
    top: 100%;
    left: 44%;
    margin-top: 5px;
}

/*遇见内容*/
.yujian_item {
    background: #fff;
    padding: 20px;
    border-bottom: 1px solid #f7f7f7;
}
.yujian_item:first-child{
    margin-top: 20px;
}

.time_ago {
    width: 15%;
    color: #999999;
    font-size: 12px;
}

.yujian_photo_box {
    width: 35px;
    height: 35px;
}

.yujian_photo_box img {
    height: 100%;
    border-radius: 35px;
}

.position_img {
    width: 10px;
}

.right_title {
    margin-left: 15px;
}

.position_text {
    color: #999999;
    font-size: 12px;
}

.yujian_content_text {
    color: #999999;
    font-size: 14px;
    margin-bottom: 10px;
}

.inner_scroll_img_box {
    margin: 10px 0;
    padding-left: 15%;
}

.scroll_item_inner_img_box {
    overflow-x: auto;
    white-space: nowrap;
}

.scroll_item_inner_img {
    width: 100px;
    height: 100px;
    margin-right: 10px;
}

.xin_num_img, .msg_num_img {
    width: 15px;
}

.yujian_xin_box {
    line-height: 30px;
}

.yujian_xin_box span {
    margin-left: 5px;
    font-size: 15px;
    color: #999999;
}

.yujian_xin_end {
    margin-left: 15px;
}