.tab_bar_content {
    width: 100%;
    height: 70px;
    position: fixed;
    bottom: 0;
    left: 0;
    background: #fff;
}

.tab_bar_box {
    display: flex;
    align-items: center;
    height: 100%;
}

.tab_bar_item {
    list-style: none;
    flex: 1;
    text-align: center;
}

.tab_bar_item img {
    width: 22px;
    height: 22px;
}

.tab_bar_item .jia {
    width: 50px;
    height: 50px;
}

.tab_bar_text {
    font-size: 12px;
}