.black_img {
    width: 12px;
    position: absolute;
    left: 4%;
    top: 5%;
    z-index: 999;
}

.user_top {
    height: 200px;
    position: relative;
}

.user_top_info {
    background: #ffffff;
    width: 90%;
    margin-left: 5%;
    position: absolute;
    top: 55%;
    border-radius: 5px;
    padding-bottom: 25px;
}

.user_photo {
    width: 50px;
    height: 50px;
    position: absolute;
    left: 50%;
    margin-left: -25px;
    top: -13%;
}

.user_photo img {
    height: 100%;
    border-radius: 50px;
}

.user_name_box {
    margin-top: 10%;
}

.user_name_state {
    text-align: center;
    position: relative;
    border-bottom: 1px solid #eeeeee;
    padding-bottom: 10px;
}

.user_name {
    font-size: 16px;
}

.user_state {
    color: #999999;
    font-size: 12px;
    margin-top: 6px;
}

.user_love_btn {
    background: #ff7153;
    color: #ffffff;
    font-size: 12px;
    padding: 6px 12px;
    border: unset;
    font-weight: bold;
    border-radius: 5px;
    position: absolute;
    right: 5px;
    top: 0;
}

.zuopin_box {
    margin-top: 25px;
}

.zuopin_box div {
    flex: 1;
    text-align: center;
}

.zuopin_box div p:first-child {
    color: #333333;
    font-size: 13px;
}

.zuopin_box div p:last-child {
    color: #999999;
    font-size: 16px;
    margin-top: 5px;
}

.rongyu_title {
    color: #999999;
}

.level {
    color: gold;
    font-size: 12px;
    margin-left: 10px;
}

/*用户荣誉*/
.user_rongyu_box {
    margin-top: 25%;
    background: #ffffff;
    padding: 20px 5%;
}

.progress {
    background: #ffe6e1;
    height: 12px;
    border-radius: 10px;
    width: 100%;
}

.progress_inner {
    width: 80%;
    height: 100%;
    background: #ff6c4e;
    border-radius: 10px;
}

.progress_num {
    text-align: right;
    width: 70%;
    color: #ff6c4e;
}

.user_zan_img {
    width: 30px;
    margin-left: 15px;
}

/*作品*/
#works {
    padding: 20px 5%;
    background: #ffffff;
    margin-top: 10px;
}

.works_content_item {
    padding: 20px 0;
    border-bottom: 1px solid #f7f7f7;
}

.author_photo {
    width: 40px;
    height: 40px;
    border-radius: 40px;
}

.author_info {
    margin-left: 10px;
}

.position_img {
    width: 10px;
}

.position_text {
    color: #999999;
    font-size: 12px;
}

.release_time {
    color: #999999;
    font-size: 12px;
}

.works_content_body {
    margin-top: 10px;
}

.works_content_body_tips {
    color: #999999;
    font-size: 13px;
}

.works_content_body_img_box {
    width: 100px;
    height: 100px;
    background: #999999;
}

.works_content_body_img_box img {
    width: 100%;
    height: 100%;
}

.works_content_scroll_box {
    overflow-x: auto;
    white-space: nowrap;
}

.works_content_body_img_box {
    display: inline-block;
    margin: 0 5px;
    margin-top: 10px;
}

.xin_box {
    color: #999999;
    font-size: 15px;
    line-height: 30px;
}
.xin_box span{
    margin-left: 5px;
}

.xin_box div:last-child {
    margin-left: 5%;
}

.xin_img {
    width: 10px!important;
}