#head {
    padding: 20px;
    background: #ffffff;
}

.msg_title {
    font-size: 22px;
    position: relative;
}

p.msg_text:after {
    content: "";
    width: 5px;
    height: 5px;
    background: red;
    position: absolute;
    border-radius: 3px;
    right: 2px;
    top: 4px;
}

.msg_more {
    color: #999999;
    font-weight: bold;
}

.msg_item {
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    background: #fff;
    margin-top: 10px;
}

.msg_item.hudong_box {
    border-top: 1px solid #f7f7f7;
    border-bottom: 1px solid #f7f7f7;
}

.msg_item.hudong_box, .msg_item.shoucang_box {
    margin: 0;
}

.msg_img {
    width: 40px;
}

.tongzhi_text {
    margin-left: 15px;
    font-size: 16px;
    color: #333;
}

.right_lt {
    color: #999999;
}