.banner {
    background: url("../../static/images/banner_bg.png") no-repeat;
    height: 160px;
    padding-top: 49px;
}

.search_input_box {
    text-align: center;
    position: relative;
    display: flex;
    align-items: center;
}

.input_inner_box {
    margin-left: 8%;
}

.search_input {
    width: 235px;
    height: 23px;
    outline: none;
    border-radius: 33px;
    background: rgba(255, 255, 255, 0.3);
    border: unset;
    font-size: 20px;
    caret-color: #d13f18;
    color: #ffffff;
    padding: 10px;
    padding-left: 45px;
}

::-webkit-input-placeholder {
    color: #ffffff;
}

::-moz-placeholder {
    color: #ffffff;
}

::-ms-input-placeholder {
    color: #ffffff;
}

.banner_msg {
    width: 25px;
    margin-left: 4%;
}

.input_search_icon {
    position: absolute;
    width: 20px;
    left: 12%;
    top: 30%;
}

/*nav*/
.nav_content {
    display: flex;
    align-items: center;
    padding-top: 25%;
    padding-bottom: 17px;
    border-bottom: 1px solid #eeeeee;
    background: #fff;
}

.nav_content .nav_item {
    flex: 1;
    text-align: center;
}

.nav_content .nav_img {
    width: 50px;
}

.text_align_center {
    text-align: center;
}

.go_box, .toutiao_box {
    background: #fff;
}

.go_box img {
    width: 90%;
}

/*toutiao*/
.toutiao_box {
    margin-top: 10px;
    border-bottom: 1px solid #eeeeee;
}

.toutiao_box .toutiao_title {
    height: 30px;
    border-bottom: 1px solid #f7f7f7;
    display: flex;
    align-items: center;
    padding: 10px 15px;
    justify-content: space-between;
}

.toutiao_title img {
    width: 25%;
}

.toutiao_box .toutiao_text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 12px;
    margin-left: 20px;
}

.cipian_box {
    display: flex;
    align-items: center;
    padding: 8px 15px;
}

.cipian_right {
    margin-left: 10px;
}

/*推荐*/
.tuijian_box {
    margin-top: 10px;
}

.tuijian_box .tuijian_head {
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #ffffff;
    padding: 8px 15px;
    color: #999999;
}

.tuijian_head div {
    display: flex;
    align-items: center;
}

.tuijian_head img {
    width: 15px;
}

.tuijian_head .tuijian_more {
    position: relative;
}

.tuijian_head .tuijian_more:after {
    content: "";
    width: 5px;
    height: 5px;
    border-radius: 2px;
    background: red;
    position: absolute;
    right: 0;
    top: 0;
}

.tuijian_content {
    padding: 8px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.tuijian_content_item {
    width: 40%;
    background: #ffffff;
    padding: 15px;
    display: inline-block;
    position: relative;
}

.tuijian_content-item_head {
    text-align: center;
}

.tuijian_content-item_head img {
    width: 70%;
}

.tuijian_info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
}

.tuijian_info img {
    width: 18px;
}

.new_9 {
    color: #999999;
    font-size: 14px;
}

.tuijian_info_num div:first-child {
    margin: 0 2px;
}

.tuijian_info_num span {
    font-size: 14px;
}

.jifen {
    color: #ff5e50;
    font-weight: bold;
}

.different_qu {
    position: absolute;
    left: 0;
    top: 15px;
    color: #fff;
    padding: 1px 8px 1px 1px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}