body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tab_bar_content {
    width: 100%;
    height: 70px;
    position: fixed;
    bottom: 0;
    left: 0;
    background: #fff;
}

.tab_bar_box {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.tab_bar_item {
    list-style: none;
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
}

.tab_bar_item img {
    width: 22px;
    height: 22px;
}

.tab_bar_item .jia {
    width: 50px;
    height: 50px;
}

.tab_bar_text {
    font-size: 12px;
}
.banner {
    background: url(/static/media/banner_bg.5d2e3ed0.png) no-repeat;
    height: 160px;
    padding-top: 49px;
}

.search_input_box {
    text-align: center;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.input_inner_box {
    margin-left: 8%;
}

.search_input {
    width: 235px;
    height: 23px;
    outline: none;
    border-radius: 33px;
    background: rgba(255, 255, 255, 0.3);
    border: unset;
    font-size: 20px;
    caret-color: #d13f18;
    color: #ffffff;
    padding: 10px;
    padding-left: 45px;
}

::-webkit-input-placeholder {
    color: #ffffff;
}

::-moz-placeholder {
    color: #ffffff;
}

::-ms-input-placeholder {
    color: #ffffff;
}

.banner_msg {
    width: 25px;
    margin-left: 4%;
}

.input_search_icon {
    position: absolute;
    width: 20px;
    left: 12%;
    top: 30%;
}

/*nav*/
.nav_content {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 25%;
    padding-bottom: 17px;
    border-bottom: 1px solid #eeeeee;
    background: #fff;
}

.nav_content .nav_item {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
}

.nav_content .nav_img {
    width: 50px;
}

.text_align_center {
    text-align: center;
}

.go_box, .toutiao_box {
    background: #fff;
}

.go_box img {
    width: 90%;
}

/*toutiao*/
.toutiao_box {
    margin-top: 10px;
    border-bottom: 1px solid #eeeeee;
}

.toutiao_box .toutiao_title {
    height: 30px;
    border-bottom: 1px solid #f7f7f7;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px 15px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.toutiao_title img {
    width: 25%;
}

.toutiao_box .toutiao_text {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 12px;
    margin-left: 20px;
}

.cipian_box {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 15px;
}

.cipian_right {
