}

/*推荐*/
.tuijian_box {
    margin-top: 10px;
}

.tuijian_box .tuijian_head {
    height: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: #ffffff;
    padding: 8px 15px;
    color: #999999;
}

.tuijian_head div {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.tuijian_head img {
    width: 15px;
}

.tuijian_head .tuijian_more {
    position: relative;
}

.tuijian_head .tuijian_more:after {
    content: "";
    width: 5px;
    height: 5px;
    border-radius: 2px;
    background: red;
    position: absolute;
    right: 0;
    top: 0;
}

.tuijian_content {
    padding: 8px 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.tuijian_content_item {
    width: 40%;
    background: #ffffff;
    padding: 15px;
    display: inline-block;
    position: relative;
}

.tuijian_content-item_head {
    text-align: center;
}

.tuijian_content-item_head img {
    width: 70%;
}

.tuijian_info {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 10px;
}

.tuijian_info img {
    width: 18px;
}

.new_9 {
    color: #999999;
    font-size: 14px;
}

.tuijian_info_num div:first-child {
    margin: 0 2px;
}

.tuijian_info_num span {
    font-size: 14px;
}

.jifen {
    color: #ff5e50;
    font-weight: bold;
}

.different_qu {
    position: absolute;
    left: 0;
    top: 15px;
    color: #fff;
    padding: 1px 8px 1px 1px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}
