}

p.yujian_text:after {
    content: "";
    width: 5px;
    height: 5px;
    background: red;
    position: absolute;
    border-radius: 3px;
    right: 2px;
    top: 2px;
}

.scroll_box {
    overflow-x: auto;
    white-space: nowrap;
    padding: 0 20px;
    margin-top: 10px;
}

.scroll_item {
    padding: 0 20px;
    width: 150px;
    height: 90px;
    background: #ffffff;
    border-radius: 10px;
