}

.scroll_item_img {
    width: 20px;
}

.scroll_item_inner_div {
    padding-top: 20px;
}

.scroll_item_text {
    position: relative;
    font-size: 13px;
}

.scroll_item_text:after {
    content: "";
    width: 20px;
    height: 2px;
    background: #333333;
    position: absolute;
    top: 100%;
    left: 44%;
    margin-top: 5px;
}

