* {
    margin: 0;
    padding: 0;
}

body, html {
    margin: 0;
    padding: 0;
    background: #f7f7f7;
    padding-bottom: 35px;
}

a {
    color: #999999;
}

a:link, a:visited, a:hover, a:active {
    text-decoration: none;
    color: #ff5e50;
}

img {
    width: 100%;
}

.flex {
    display: flex;
}

.flex.align_items_center {
    align-items: center;
}
.flex.justify-content-space-between{
    justify-content: space-between;
}
.flex.justify-content-flex-end{
    justify-content: flex-end;
}